.author_photo img {
    border-radius: 30px;
    width: 50px;
    height: 50px;
}

.profile_author {
    align-items: center!important;
    display: flex!important;
    font-size: 14px;
    padding-bottom: 10px;
}

.name_author {
    line-height: 1.5;
    margin-left: 10px;
}

.time_publ {
    margin: 0;
    font-weight: 300;
}

.posted_content p{
    font-size: 15px;;
    font-weight: 300;
    line-height: 1.7;
}

.post_meta_like span{
    margin: auto;
    color: #666;
    margin-right: 10px;


}

.post_meta {

    padding: 0 20px;
    font-size: 12px;
    justify-content: space-between;
    display: flex;
    margin: 20px 0 0 0;

}

.post_meta i{
    color: #666;
    font-size: 20px;
    margin: auto 0;
    padding-left: 10px;
    padding-right: 10px;

}

.com_share {
    display: flex;
}

.like strong {
    opacity: 0;
}

.post_meta_like i:hover {
    color: #d84639;
}



