*,
*:before,
*:after {
  box-sizing: border-box;
}
h1,
h2,
h3,
h4,
h5,
h6{
  margin: 0;
}

textarea {
  resize: none;
}

.btn, button {
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  background-color: transparent;
}

img {
  max-width: 100%;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}

.app-social {
  background-color: #f1f1f1;

  grid-template-areas: "h f m";
  grid-template-rows:  1fr 1fr 1fr;
  grid-gap: 5px;
  font-family: 'Roboto', sans-serif;
  font-weight: 700;

}

.app-social .active {
  color: #dc4734;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;

  max-width: 1200px;
  display: grid;
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  
}

.app-social a:hover {
  color: #dc4734;
}

 .app-social a {
  transition: .2s linear;

  display: flex;
  margin-right: 15px;
  /* font-size: 16px; */
  text-decoration: none;
  color: #333;
}

 .default_photo {
   width: 30px;
   border-radius: 10000px;
 }

.row {
  display: flex;
}

.card {
  background-color: white;
  -webkit-box-shadow: 0px 1px 15px 0px rgba(51, 51, 51, 0.2);
  box-shadow: 0px 1px 15px 0px rgba(51, 51, 51, 0.2);

  padding: 5px 20px 10px 25px;
}

ul li {
  list-style: none;
}

.card-post {
  padding: 25px;
  margin-bottom: 30px;
}

.col-md-1 {
  display: flex;
}

.col-md-1 a{
  display: inline-block;
  margin-right: 60px;
  position: relative;
}

.col-lg-1 {
  flex: 0 0 41%;
  max-width: 41%;
}

.col-lg-2 {
  flex: 0 0 16%;
  max-width: 16%;
}

.col-lg-3 {
  flex: 0 0 43%;
  max-width: 43%;
}

.col-md-3 {
  display: flex;
  justify-content: flex-end;
}

.col-lg-4 {
  flex: 0 0 25%;
  max-width: 25%;
}

.col-lg-5 {
  flex: 0 0 50%;
  max-width: 50%;
}

.col-lg-6 {
  flex: 0 0 16.666667%;
  max-width: 16.666667%;
}
col-friends {
  width: 100%;
}

.order-2{
  top: 0;
  padding: 20px 15px;
}
