.about {
    margin: 20px;
    padding: 15px;

    box-shadow: 0px 1px 15px 0px rgba(51, 51, 51, 0.2);
    background-color: white;
}

.about_head {
    text-align: center;
}

.about_title {
    color: #dc4734;
}

.about_title:after {
    content: "";
    display: block;
    width: 460px;
    height: 1px;
    margin: 10px auto;
    background-image: linear-gradient(to right, white, #dc4734, white);
}

.about_nav {
    display: flex;
    justify-content: space-between;
}

.about_nav_l {
    display: flex;
    flex-wrap: wrap;
}

.about_nav_title {
    text-align: center;
    width: 33.333333%;
    padding-top: 15px;
    font-size: 14px;
    font-weight: 300;
    padding-bottom: 3px;
    border-bottom: 1px solid #ccc;

    transition: .2s linear;

}

.about_nav_title:hover {
    color: #dc4734;
    border-color: #dc4734;
}

.about_social {

    display: flex;
    justify-content: space-between;

    font-size: 12px;

    padding: 15px 20px 0 20px;
}

.about_name_author {
    border-bottom: 1px solid #dddddd;
    padding-bottom: 20px;
    padding-top: 10px;
}

.about_text_author {
    padding: 10px 0;

    font-size: 14px;
    font-weight: 300;
    line-height: 1.5;
}
