.card_dialog-action {
    min-height: 400px;
    position: relative;

    margin: 20px;
    padding: 0;
}


.top_bar {
    display: flex;

    border-bottom: 1px solid #ddd;
}

.col-dg-2 {
    margin: auto;
}

.col-dg-3 {
    display: flex;
    justify-content: flex-end;

    margin: auto 0;
}

.btn_dialog-back {
    color: #666666;
    display: flex;
    margin: auto 0;
    font-size: 14px;
    padding: 7px 40px 7px 20px;

}

.btn_dialog-back i{
    margin: auto 0;
    font-size: 22px;
}

.btn_dialog-back p{
    margin: auto 0;
    padding-left: 10px;

}

.col-dg-1:hover {
    background: linear-gradient(to right, #ccc, white);
}

.right_dialog-bar {

    margin: auto 0;
}

.profile-thumb-small-diag {
    margin: 0 10px;
    width: 30px;
    height: 30px;
}

.dialog_name {
    text-align: center;
    font-size: 14px;
}

.dialog_input {
    padding: 10px 0;
    width: 100%;
    bottom: 0;
    position: relative;

    border-top: 1px solid #ddd;
}

.dialog_input .profile_search_form{
    padding-right: 15px;

}

.dialog_input .profile_search_form .profile_btn {
    right: 3.5%;
}

.dialog_item {
    display: flex;
    height: 100%;
    width: 100%;

    padding: 15px;
}

.dialog_item-photo {

}

.dialog_item_msg {
    font-size: 14px;
    font-weight: 300;
}

.dialog_name_msg {
    font-size: 13mpx;
    font-weight: 500;
    padding-bottom: 10px;

}