.main {
    width: 100%;
    position: relative;
    grid-area: m;
}

figure {
  margin: 0;
}

.profile-banner-large {
    background-image: url(./../../assets/img/profile-banner.jpg);
    height: 300px;
}

.profile-menu-area {
    padding: 10px 0;
}

.edit_profile {
    cursor: pointer;
    margin-left: 110px;
    font-size: 15px;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    border: 0;
    background-color: #dc4734;
    height: 35px;
    width: 120px;
    color: white;
    border-radius: 30px;

    transition: .2s linear;
}

.edit_profile:hover {
    background-color: #333;
}

.edit_profile:focus {
    outline: none;
}

.bg-white {
    background-color: white;
}

.align-items-center {
    align-items: center !important;
}

.col-md-5 {
    display: flex;
    justify-content: flex-end;
}

.col-md-5 a {
    display: inline-block;
    margin-right: 60px;
    position: relative;
}

.col-md-5 a:last-child {
    padding-right: 0;
    margin-right: 0;
}

.profile-picture-box {
    margin-left: 15px;
    width: 270px;
    position: absolute;
    -webkit-transform: translateY(calc(-50% - 14px));
    -ms-transform: translateY(calc(-50% - 14px));
    transform: translateY(calc(-50% - 14px));
    background-color: #fff;
    z-index: 1;
}

.profile-picture {
  position: relative;
}

.profile-picture:before {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  content: '';
  position: absolute;
  pointer-events: none;
  border: 10px solid #fff;
}

.profile-picture a{
  margin: 0;
}