  .card-small {
    padding: 15px;
    margin-bottom: 30px;
  }

  .w-100 {
    width: 100%!important;
  }

  .profile-menu_search {
    display: inline-flex;
  }

  .profile_search {
    width: 100%!important;
  }

.share-box-inner {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

.profile_search_form {
  position: relative;
  padding-left: 15px;
}

  .profile_search_form .share-text-field {
    width: 100%;
    border: none;
    display: block;
    height: 38px;
    padding: 12px 90px 13px 20px;
    border-radius: 50px;
    overflow: hidden;
    background-color: #f1f1f1;
    -webkit-box-shadow: inset 0px 1px 10px 0px rgba(85, 85, 85, 0.2);
    box-shadow: inset 0px 1px 10px 0px rgba(85, 85, 85, 0.2);
    -webkit-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
  }

  .profile_search_form .profile_btn {
    top: 50%;
    right: 0.5%;
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    line-height: 1;
    padding: 8px 22px;
    margin-right: 2px;
    border-radius: 40px;
    background-color: #dc4734;
    position: absolute;
    text-transform: uppercase;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
  }

  .profile_btn:focus, .share-text-field  {
    outline: none;
  }

  .profile_btn {
    border: 0;
    cursor: pointer;

    transition: .2s linear;
  }

  .profile_btn:hover {
    background-color: #333;
  }