.friends_menu {
    margin: 20px;
    padding: 15px;

    box-shadow: 0px 1px 15px 0px rgba(51, 51, 51, 0.2);
    background-color: white;
}

.friends_menu_inner {
    display: flex;
    justify-content: space-between;
}

.friends_inner {
    flex: 0 0 45%;
    max-width: 45%;
    display: flex;

    color: #333;
    padding: 20px 15px 15px 15px;
    margin-left: 20px;
    margin-bottom: 20px;
    box-shadow: 0px 1px 15px 0px rgba(51, 51, 51, 0.2);
    background-color: white;

    transition: .2s linear;
}

.friends_inner:hover button {
    color: white;
}

.friends_inner:hover a {
    color: white;
}

.friends_inner:hover {
    color: white;
    background-color: #dc4734;
}

.friends_page {
    flex-wrap: wrap;
    display: flex;
}

.friends_nav {
    display: flex;
}

.friends_nav div {
    padding-left: 20px ;
    font-weight: 300;
}

.photo_friends img{
    margin-right: 20px;
    width: 40px;
    border-radius: 30px;
}

.name_friends a{
    font-size: 14px;
    padding-bottom: 5px;
}

.invite_friends button{
    font-size: 13px;
    font-weight: 300;
}

.invite_btn_unfollow {
    color: #999;
}

.get_users_btn {
    margin: 0 auto;
    padding: 10px 25px;

    font-size: 14px;
    font-weight: 700;
    color: white;

    border: 1px solid #dc4734;
    border-radius: 25px;
    background-color: #dc4734;
}

.get_users_btn:hover {
    border-color: #333;
    background-color: #333;
}

.name_friends a:hover{
    color: #ccc;
}