.post_settings_bar {
    margin-left: auto;
    position: relative;
    cursor: pointer;
}

.post_settings_bar span {
    width: 20px;
    height: 1px;
    display: block;
    background-color: #ccc;
    margin-bottom: 4px;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;

}

.post_settings_bar:hover span{
    background-color: #dc4734;
}

.post_settings_bar:hover .post-settings{
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateY(15px);
    -ms-transform: translateY(15px);
    transform: translateY(15px);
}


.post-settings {
    right: 0;
    top: 100%;
    width: 200px;
    background-color: #fff;
    position: absolute;
    padding: 20px;
    z-index: 9;
    border-radius: 5px;
    border: 1px solid #e5e5e5;
    -webkit-box-shadow: 0px 1px 15px 0px rgba(51, 51, 51, 0.2);
    box-shadow: 0px 1px 15px 0px rgba(51, 51, 51, 0.2);
    opacity: 0;
    visibility: hidden;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
    -webkit-transform: translateY(30px);
    -ms-transform: translateY(30px);
    transform: translateY(30px);
}

.post_settings_bar {
    margin-left: auto;
    position: relative;
    cursor: pointer;
}

.post_settings_bar span {
    width: 20px;
    height: 1px;
    display: block;
    background-color: #ccc;
    margin-bottom: 4px;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
}

.post_settings_bar:hover span{
    background-color: #dc4734;
}

.post-settings:hover {
    -webkit-transform: translateY(15px);
    -ms-transform: translateY(15px);
    transform: translateY(15px);

}

.arrow-shape:before {
    right: 10px;
    bottom: 100%;
    content: "";
    position: absolute;
    height: 20px;
    border-bottom: 20px solid #fff;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
}

.post_settings_bar_inner button{
    color: #333;
    display: block;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 10px;
    text-transform: capitalize;

    transition: .1s linear;
}

.post_settings_bar_inner:last-child button{
    margin-bottom: 0;
}

.post_settings_bar_inner{
    padding-top: 5px;
    margin-bottom: 10px;
    padding-bottom: 5px;
    border-bottom: 1px solid #e5e5e5;
}

.post_settings_bar_inner:last-child{
    margin-bottom: 0;
    padding-bottom: 0;
    border: 0;
}

.post_settings_bar_inner:hover button{
    color: #dc4734;
}