.header {
    height: 50px;
    background-color: white;
    grid-area: h;
    display: flex;
    align-items: center!important;
  }
  .header_nav {
    display: flex;
    margin: auto 0;
    flex: 0 0 41.666667%;
    max-width: 41.666667%;
  
    font-family: 'Roboto', sans-serif;
  }
  
  .header_logo {
    margin: auto;
    flex: 0 0 16.666667%;
    max-width: 16.666667%;
  }
  
  .header_search {
    margin: auto 0;
    display: flex;
    flex: 0 0 41.666667%;
    max-width: 41.666667%;  
    justify-content: flex-end;
  }
  
  .search_field {
    min-width: 250px;
    position: relative;
    padding-bottom: 5px;
    border: 0;
    border-bottom: 1px solid #ddd;
    height: 30px;
  }
  .search_field:focus, .search_btn:focus {
    outline: none;
  }
  
  .search {
    margin-right: 15px;
    justify-content: end;
    display: flex;
  }


  .search_btn {
    border: 0;
    border-bottom: 1px solid #ddd;
    cursor: pointer;

    opacity: .7;
    background-color: transparent;
    font-size: 20px;
    position: relative;
    color: #333333;

    
  }

  .search_btn i {
  transition: .2s linear;
  }
  
  .search_btn i:hover {
    color: #d84639;
    opacity: 1;
  }